import { pages } from "projectConfig"
import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsStaticPage"
import { useAllImagesHomeSlider } from "projectGraphqlStaticQueries/images-slider-home"

export default function Offer001Page(props) {
  const allImages = useAllImagesHomeSlider()

  return (
    <>
      <GatsbySeo
        title={pages.special.offer001.title}
        description={pages.special.offer001.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.special.offer001.title,
          description: pages.special.offer001.description,
        }}
      />
      <Layout>
        <article className={"adaptive text"}>
          {allImages.map(item => {
            let imageFluid = item.node.childImageSharp.fluid
            let imageRelativePath = item.node.relativePath
            if (imageRelativePath === "assets/special-offers/carousel/001.jpg")
              return (
                <Img
                  fluid={imageFluid}
                  key={imageRelativePath}
                  className={"mb-4"}
                ></Img>
              )
          })}
          <h1 className={"mb-4"}>Скидка -10% на все товары</h1>
          <p>
            <b>Чтобы получить скидку нужно:</b>
          </p>
          <ol>
            <li>
              Поделиться ссылкой на главную страницу нашего сайта в любых трех
              соц. сетях
            </li>
            <li>
              Написать в нашу{" "}
              <Link to={"/contacts"}>техническую поддержку</Link>. В обращении
              указать прямый ссылки на ваши публикации
            </li>
            <li>
              После этого менеджер отправит вам бонус код на скидку в 10%,
              который вы сможете применить при оформлении заказа на любой наш
              товар
            </li>
          </ol>
        </article>
      </Layout>
    </>
  )
}
