import { useStaticQuery, graphql } from "gatsby"
export const useAllImagesHomeSlider = () => {
  const { allFile } = useStaticQuery(
    graphql`
      query AllImages {
        allFile(
          filter: {
            relativePath: { regex: "/assets/special-offers/carousel//" }
          }
        ) {
          edges {
            node {
              relativePath
              name
              childImageSharp {
                fluid(maxWidth: 1200, quality: 100) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    `
  )
  return allFile.edges
}
